.fa-sort-alpha-down {
  color: rgb(153, 160, 153);
  font-size: 20px;
  &:hover {
    color: rgb(175, 248, 157);
  }
}
.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(181, 211, 173);
}
.header__title {
  font-size: 60px;
  font-family: "Harry Potter", sans-serif;
  margin-bottom: 0px;
  -webkit-text-stroke: 1.5px rgb(0, 0, 0);
  background: linear-gradient(rgb(203, 229, 192), rgb(15, 15, 15));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.header__subtitle {
  margin-top: 0px;
  font-family: "IM Fell English SC", serif;
  font-size: 25px;
}

.main {
  @include tablet {
    width: 70%;
    display: flex;
    flex-direction: row;
  }
  @include desktop {
    width: 80%;
    display: flex;
    flex-direction: row;
  }
  &__container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    @include tablet {
      flex-wrap: nowrap;
    }
    @include desktop {
      flex-wrap: nowrap;
    }
  }
  &__section {
    &--container {
      display: flex;
      height: 500px;
      justify-content: center;
      width: 100%;
      flex-wrap: wrap;

      &--element {
        width: 300px;
        height: 300px;
      }
    }
    &--element {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    &--name {
      color: rgb(210, 233, 210);
      font-size: 20px;
      padding: 5px;
      font-family: "IM Fell English SC", serif;
    }
    &--image {
      height: 200px;
      width: 150px;
      border-radius: 8px;
      box-shadow: 0px 0px 3px 3px rgb(205, 216, 205);
    }
    &--list {
      padding-top: 5px;
      padding-bottom: 5px;
    }
  }
}
.section {
  height: 400px;
  padding: 8px;
  margin-left: 12px;
  display: flex;
  justify-content: colum;
  flex-wrap: wrap;
  background: linear-gradient(
    rgba(108, 110, 105, 0.795),
    rgba(15, 15, 15, 0.808)
  );
  
  border-radius: 12px;
  padding: 8px;
  margin-left: 12px;

  color: rgb(198, 207, 202);
  @include tablet {
    width: 200px;
    padding: 8px;
    margin-left: 12px;
    height: 380px;
    display: flex;
    flex-direction: column;
  }
  @include desktop {
    padding: 8px;
    margin-left: 12px;
    height: 380px;
    display: flex;
    flex-direction: colum;
  }

  &__title {
    padding: 8px;
    margin: 0px;
    font-family: "IM Fell English SC", serif;
    font-size: 16px;
  }
  &__filter {
    display: flex;
    flex-direction:column;
    width: 160px;
    font-size: 8px;  
    justify-content: center;
    align-items: center;

     @include tablet {
      width: 160px;
      margin-left: 12px;
      display: flex;
      flex-direction: column;
    }
    @include desktop {
      width: 160px;
      margin-left: 12px;
      display: flex;
      flex-direction: colum;
    }

    &--button {
      margin-left: auto;
      margin-right: auto;
      padding-top: 8px;
      padding-bottom: 12px;
      background-color: transparent;
      border: transparent;
      cursor: pointer;
      cursor: hand;
      @include tablet {
        padding-bottom: 22px;
      }
      @include desktop {
        padding-bottom: 22px;
      }

      
    }
  }
}
.filter {
  &__label {
    font-size: 16px;
    padding: 8px;
    @include tablet {
      font-size: 20px;
    }
    @include desktop {
      font-size: 22px;
    }
  }
  &__input {
    width: 140px;
    height: 20px;
    font-size: 18px;
    background: darkgrey;
    border-radius: 6px;
    border: 1px solid darkgrey;
    font-family: "Markazi Text", serif;
    padding-left: 8px;
    
  }
  &__select {
    font-family: "Markazi Text", serif;
    font-size: 18px;
    height: 28px;
    width: 140px;
    background: darkgrey;
    border-radius: 6px;
    border: 1px solid darkgrey;
    margin-bottom: 12px;
  }
}


.rest__button {
  background-color: rgb(84, 87, 84);
  color: rgb(201, 201, 201);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 3px;
  width: 80px;
  height: 40px;
  border-radius: 8px;
  border: transparent;
  cursor: pointer;
  cursor: hand;
  font-family: "Markazi Text", serif;
  font-size: 14px;
  &:hover {
    background-color: rgb(201, 201, 201);
    color: rgb(84, 87, 84);
  }
  @include tablet {
    width: 140px;
    height: 30px;
    font-size: 16px;
  }
  @include desktop {
    width: 140px;
    height: 30px;
    font-size: 16px;
  }
}
